<template>
<div class="container">
    <h1></h1>

    <b-alert 
    :show="dismissCountDown" 
    dismissible 
    :variant="mensaje.color" 
    @dismissed="dismissCountDown=0" 
    @dismiss-count-down="countDownChanged" > 
    {{mensaje.texto}} 
    </b-alert>


    <!--<form @submit.prevent="editarNota(notaEditar)" v-if="editar">
        <h3>Editar nota</h3>

        <input type="text" class="form-control my-2" placeholder="Nombre" v-model="notaEditar.nombre">
        <input type="text" class="form-control my-2" placeholder="Descripcion" v-model="notaEditar.descripcion">
        <b-button class="btn-success my-2 mx-2" type="submit">Editar</b-button>
        <b-button class=" my-2" type="submit" @click="editar=false">Cancelar</b-button>

    </form>-->
    <form @submit.prevent="agregarNota()" v-if="!editar">
        <h3>Formulario Registro</h3>

        <input class="controls" type="text" name="nombres" id="nombres" placeholder="Ingrese su Nombre" v-model="nota.nombre">
        <input class="controls" type="text" name="apellidos" id="apellidos" placeholder="Ingrese su Apellido" v-model="nota.apellido">
        <input class="controls" type="email" name="correo" id="correo" placeholder="Ingrese su Correo" v-model="nota.correo">
        <input class="controls" type="password" name="correo" id="correo" placeholder="Ingrese su Contraseña" v-model="nota.contraseña">
        <div class="modal-footer">
        <b-button class="btn-success my-2" type="submit">Confirmar Registro</b-button>
        <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Cancelar</button>
        <p>Estoy de acuerdo con <a href="#">Terminos y Condiciones</a></p>
        <p><a href="#">¿Ya tengo Cuenta?</a></p>
        </div>

    </form>

    <table class="table">
        <thead>
            <tr>
               
            </tr>
        </thead>
        <tbody>
            <tr v-for="(item, index) in notas" :key="index">
                <!--<th scope="row">{{item._id}}</th>
                <td>{{item.nombre}}</td>
                <td>{{item.apellido}}</td>
                <td>
                    <b-button class="btn-danger mx-2" @click="eliminarNota(item._id)">Eliminar</b-button>
                    <b-button class="btn-warning mx-2" @click="activarEdicion(item._id)">Editar</b-button>
                </td>-->
            </tr>

        </tbody>
    </table>

</div>
</template>

<script>
export default {

    data() {
        return {

            notas: [],
            mensaje: {color: 'success', texto: ''}, 
            dismissSecs: 5, 
            dismissCountDown: 0,

            nota:{nombre:"",apellido:"",correo:"",contraseña:""},
            editar:false,
            notaEditar:{}

        }

    },

    created() {

        this.listarNotas();

    },

    methods: {

        listarNotas() {

            this.axios.get('/nota')
                .then(res => {
                    console.log(res.data);
                    this.notas = res.data;

                })
                .catch(e => {

                    console.log(e.response);

                })

        },

        agregarNota(){


            this.axios.post('/nueva-nota',this.nota)
            .then(res=>{

                this.notas.push(res.data)
                this.nota.nombre="";
                this.nota.apellido="";
                this.nota.correo="";
                this.nota.contraseña="";
                this.mensaje.color="success";
                this.mensaje.texto="Registro realizado";
                this.showAlert();


            })
            .catch(e=>{

                console.log(e.response);

            })


        },

        eliminarNota(id){

            this.axios.delete(`/nota/${id}`)
            .then(res=>{

                const index = this.notas.findIndex(item=> item._id===res.data._id);
                this.notas.splice(index, 1);
                this.mensaje.color="success";
                this.mensaje.texto="Nota Eliminada";
                this.showAlert();


            })
            .catch(e=>{

                  console.log(e.response);

            })
        },

        activarEdicion(id){

            this.editar=true;
            this.axios.get(`/nota/${id}`)
            .then(res=>{

                this.notaEditar=res.data;

            })
            .catch(e=>{

                 console.log(e.response);


            })


        },

        editarNota(item){
            
            this.axios.put(`/nota/${item._id}`, item)
            .then(res=>{
                const index= this.notas.findIndex(n=> n._id===res.data._id);
                this.notas[index].nombre=res.data.nombre;
                this.notas[index].descripcion=res.data.descripcion;
                this.mensaje.color="success";
                this.mensaje.texto="Nota Editada";
                this.showAlert();
                this.editar=false;


            })
            .catch(e=>{

                console.log(e.response);

            })



        },
        countDownChanged(dismissCountDown) { 
            this.dismissCountDown = dismissCountDown 
        }, 
        showAlert() { 
            this.dismissCountDown = this.dismissSecs 
        }

    }

}
</script>
